 /**
  * doc styles
  */
body{
    font-size: 14px;
    font-family: 'RobotoDraft', 'Roboto', 'Helvetica Neue, Helvetica, Arial', sans-serif;
    // font-family: "Helvetica Neue", Helvetica, Microsoft Yahei, Hiragino Sans GB, WenQuanYi Micro Hei, sans-serif;
}
header {
    background-color: #fff;
}

.icontext {
    font-size: 20px;
}

.layout {
    padding: 16px 0;
    margin: 8px 0;
    color: #fff;
    text-align: center;
    background: rgba(0, 160, 233, .7)
}

.layout:nth-child(even) {
    background: #00a0e9;
}

.sidebar-left {
    position: fixed;
    z-index: 20;
    background: #fff;
    width: 250px;
    min-height: 100%;
    box-shadow: 2px 0 3px rgba(0, 0, 0, .05);
    padding-top: 10px
}

.container-outer {
    padding: 0 20px 0 20px;
    margin-left: 280px;
    margin-right: 30px;
    background-color:transparent;
    background-color: #fff;
    .container-inner {
        padding: 60px 0 60px 0;
        margin: 50px 0 0 0;
    }
}
/**重写表格自适应宽度*/
.markdown-body table {
  display: inline-table;
  width: 100%;
  overflow: auto;
}
.navbar-nav{
    margin: 0;
}
.header-top{
    background: #3787dd;
    height: 60px;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 100;
    .navbar-right{
       margin-right:15px;
    }
    .nav{
        padding-left: 0;
        margin-bottom: 0;
        list-style: none;
        color: #c3dbf5;
        li>a{
            color: #c3dbf5;
            padding: 0 20px;
            height: 60px;
            line-height: 60px;
            border-right: 1px solid #367cc9;
            border-top: none;
            border-bottom: none;
            &:hover{
                background: #1c6ec7;
            }
        }
        &>li.actived>a{
            background: #3379c6;
            color: #fff;
        }
    }
    .branding{
       background: #1c6ec7;
        height: 60px;
        width: 250px;
        float: left;
        &>a{
            display: block;
            color: #fff;
            font-size: 26px;
            line-height: 54px;
            text-align: center;
            text-decoration: none;
        }
    }
}

body {
    background-image: url(data:image/png;base64,)
}



